import { createAsyncThunk } from '@reduxjs/toolkit';
import { fetchAdFormApi } from 'features/adAddForm/api';
import { IParamsThunk } from 'features/adAddForm/models/slises/adAdSlice.types';

const fetchAdForm = createAsyncThunk(
  'addAd/fetchForm',
  async ({ title, text, images, category }: IParamsThunk) => {
    const formData = new FormData();
    formData.append('title', title);
    formData.append('text', text);
    formData.append('category', category);

    images.forEach((item) => formData.append('files', item));
    return await fetchAdFormApi(formData);
  }
);

export { fetchAdForm };
