import React, { useEffect } from 'react';

import { useNavigate } from 'react-router-dom';
import { useAppSelector } from 'store/hooks';
import { getIsAuth } from 'store/selectorFunctions';

import AuthWrapper from 'pages/auth/authWrapper';
import AuthTitle from 'widgets/login/authTitle/AuthTitle';
import LoginForm from 'features/authForm/ui/loginForm';

import style from 'pages/auth/LoginForm.module.scss';

const Auth = () => {
  const isAuth = useAppSelector(getIsAuth);
  const navigate = useNavigate();

  useEffect(() => {
    if (isAuth) {
      navigate('/');
    }
  }, [isAuth]);

  return (
    <AuthWrapper
      footerContents="     "
      footerLink="/singup"
    >
      <AuthTitle></AuthTitle>
      <LoginForm style={style} />
    </AuthWrapper>
  );
};

export default Auth;
