import React, { useState, useEffect } from 'react';
import { Controller, useForm, useController } from 'react-hook-form';
import { useAppDispatch, useAppSelector } from 'store/hooks';
import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
} from '@mui/material';

import { fetchAdForm } from 'features/adAddForm/models/slises/actionCreators';

import SetImg from 'features/adAddForm/ui/setImg';
import Button from 'shared/button';

import { IFormType, Img, IParamsSubmitForm, IPrewImg } from './AddAdform.types';

import { STATESTATUS } from 'features/adAddForm/models/slises/adAdSlice.types';
import { useNavigate } from 'react-router-dom';
import { setStatusInitial } from 'features/adAddForm/models/slises/adAddSlice';

import InputWrapper from 'shared/inputWrapper';
import { getStatus } from 'store/selectorFunctions';

import style from './AddAdForm.module.scss';

const AddAdForm = () => {
  const { handleSubmit, control } = useForm<IFormType>();

  const dispatch = useAppDispatch();
  const [prewImg, setPrewImg] = useState<IPrewImg>([null, null, null, null]);

  const status = useAppSelector(getStatus);
  const navigate = useNavigate();

  useEffect(() => {
    if (status === STATESTATUS.sucsess) {
      dispatch(setStatusInitial());
      navigate('/ads');
    }
  }, [status]);

  const sumbitForm = ({ data, prewImg }: IParamsSubmitForm) => {
    const { title, text, radio } = data;
    console.log(data);
    const images = prewImg
      .filter((item): item is Img => item !== null)
      .map((img) => img.file);
    dispatch(fetchAdForm({ images, title, text, category: radio }));
  };

  return (
    <form
      onSubmit={handleSubmit((data) => sumbitForm({ data, prewImg }))}
      className={style.root}
    >
      <Controller
        rules={{ required: true }}
        control={control}
        name="title"
        render={({ field: { onChange, onBlur, name, ref } }) => (
          <InputWrapper
            onChange={onChange}
            onBlur={onBlur}
            name={name}
            ref={ref}
            element={'input'}
            title=" "
          />
        )}
      />

      <Controller
        rules={{ required: true }}
        control={control}
        name="text"
        render={({ field: { onChange, onBlur, name, ref } }) => (
          <InputWrapper
            onChange={onChange}
            onBlur={onBlur}
            name={name}
            ref={ref}
            element={'textarea'}
            title=" "
          />
        )}
      />

      <SetImg styles={style.title} prewImg={prewImg} setPrewImg={setPrewImg} />
      <Controller
        rules={{ required: true }}
        control={control}
        name="radio"
        render={({ field }) => (
          <FormControl fullWidth style={{ marginTop: '16px' }}>
            <RadioGroup
              {...field}
              row
              aria-labelledby="demo-radio-buttons-group-label"
              defaultValue="female"
              name="radio-buttons-group"
              style={{ justifyContent: 'space-around' }}
            >
              <FormControlLabel
                value="found"
                control={<Radio />}
                label=""
              />
              <FormControlLabel
                value="exchange"
                control={<Radio />}
                label=""
              />
              <FormControlLabel value="seek" control={<Radio />} label="" />
            </RadioGroup>
          </FormControl>
        )}
      />

      <Button styles={style.btn}></Button>
    </form>
  );
};

export default AddAdForm;
